import React, {Component} from 'react';


class Ratings extends Component {
    constructor(props) {
        super(props)
        this.state={
             //    
            //
            num:this.props.ratings/2,
            //
            arr:[1,2,3,4,5]
        }
    }
    render(){
        return(
            <span>
                {
                this.state.arr.map((ele,index)=>{
                    return(
                        <span key={index}>
                            {ele>=this.state.num?<span style={{color:"#FFAC2D",fontSize:"14px"}}></span>:<span style={{color:"#FFAC2D",fontSize:"14px"}}></span>}
                        </span>
                    )
                })
                }
            </span>
    )
    }
}

export default Ratings
